@import url(https://fonts.googleapis.com/css?family=Nunito&display=swap);
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu',
        'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        -webkit-animation: App-logo-spin infinite 20s linear;
                animation: App-logo-spin infinite 20s linear;
    }
}

.App-link {
    color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

html,body{scroll-behavior:smooth;height:100%}::-webkit-scrollbar{width:0px}*{font-family:"Nunito",sans-serif}p{font-size:22px}a{font-size:22px}hr{width:12%;border:0;border-top:4px solid #a5b0da;margin:2.5em auto 2.3em}

.intro{background-image:-webkit-linear-gradient(0deg, #272626 50%, #f8f9fa 50%);height:90vh;width:100%}.intro-name:after{content:'';display:block;margin:0 auto;width:40%;padding-top:20px;border-bottom:4px solid #a5b0da}@media (max-width: 480px){.intro-left{font-size:30px}.intro-right{font-size:20px}}@media (min-device-width: 1025px){.intro-left,.intro-right{font-size:34px}}

.navbar{padding:29px;font-size:20px;background-color:#f8f9fa !important}.navbar-brand{font-family:'Nunito', sans-serif;font-size:24px;letter-spacing:3px;text-transform:uppercase}@media (min-width: 992px){.nav-link{padding-right:0.5rem;padding-left:0.5rem}}@media (min-width: 1025px){.navbar-collapse{position:absolute;left:50%;-webkit-transform:translate(-50%);transform:translate(-50%);letter-spacing:2px}.nav-item{background-color:#272626;padding:30px;margin-right:-1px}.nav-link{color:white !important;font-weight:300 !important;display:block;padding:0.5rem 1rem;text-transform:uppercase}}

.about-me{background-color:#272626;width:100%;height:500px;color:whitesmoke}

.work{background-color:rgba(187,187,187,0.24);width:100%;min-height:100%}

.projects{background-color:rgba(205,205,205,0.24);width:100%}

.card{box-shadow:0 10px 16px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19) !important;border:none;-webkit-transition:all 0.3s ease-in-out;transition:all 0.3s ease-in-out}.card:hover{-webkit-transform:scale(1.05);transform:scale(1.05);cursor:pointer}

.light-dark{color:whitesmoke;background-color:#272626}.dark{background-color:#131212;color:whitesmoke}.footer-text{font-size:18px}.pb-5,.py-5{padding-bottom:3.2rem !important}

